
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
data() {
return {
username: "",
password: "",
position: "default",
showPositionValue: false,
toastTxt: "",
show1: false,
text1: "Processing"
};
},
directives: {
TransferDom
},
created() {},
components: {
XHeader,
Box,
Group,
XButton,
Flexbox,
FlexboxItem,
XInput,
Toast,
Loading
},
methods: {
login() {
if (!this.username || !this.password) {
this.showPositionValue = true;
this.toastTxt = "用户名或密码不能为空";
return false;
}
if (this.username == "123@123.com" && this.password == "123") {
this.inTo();
} else {
this.showPositionValue = true;
this.toastTxt = "用户名或密码不正确";
}
},
inTo() {
this.$vux.loading.show({
text: "Loading"
});
setTimeout(() => {
this.$router.push({ path: "/home" });
this.$vux.loading.hide();
}, 300);
},
reset() {
this.username = "";
this.password = "";
}
}
};



